// 可以删除
var util = require('@/common/util.js');
const qiniuUploader = require("@/components/qiniu-wxapp-sdk/sdk/qiniuUploader");
export default {
	data() {
		return {
			userForm: {
				name: '',
				user_head: '',
				avator: '',
				phone: '',
				sex: 1,
				school: 1,
				start_time: '2016-08',
				end_time: '',
				hobby: "",
				education: '',
				studentPhoto: '',
				graduationPhoto: ''
			},
			defaultAvator: 'https://hudongmsite.oss-cn-hangzhou.aliyuncs.com/meeting/basicYrC5Sh1570383607306.png',
			userInfo: {},
			isEdit: false,
			imgList: [],
			imgList1: [],
			imgList2: [],
			index: -1,
			xueliList: ['专科', '本科', '硕士', '博士'],
			certificates: 1,
			content: '',
			modalName: null
		}
	},
	onLoad() {
		this.$store.dispatch('getusermsg')
		this.getUserInfo()
		this.isEdit = false
	},
	onShow() {
		if (!this.isEdit) {
			this.getUserInfo()
		}
	},
	methods: {
		goUserSetting() {
			uni.navigateTo({
				url: '/pages/user/user'
			});
		},
		
		getUserInfo () {
			this.$api.getUserInfo().then(res => {
                this.userInfo = res.data
			}).catch(err => {
			})
		},
		
		about(){
			uni.showToast({
				icon: 'none',
				title: '该模块暂未开放'
			});
		},
		
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		
		hideModal(e) {
			this.modalName = null
		},
		
		clear(){
			uni.showModal({
				title: '提示',
				content: '确定清除缓存吗？',
				cancelText: '取消',
				confirmText: '确定',
				success: res => {
					if (res.confirm) {
						setTimeout(function(){
							uni.showToast({
								 icon: 'none',
								 title: '清除成功~'
							});
						},300) 
					}
				}
			})
		},
		
		submit() {
			this.$api.proposalReported({
				proposal_type: 1,
				content: this.content
			}).then(res => {
				this.modalName = null
				 uni.showToast({
					 icon: 'none',
					 title: '提交成功,感谢您的反馈~'
				 });
			}).catch(err => {
				this.modalName = null
			})
		},
		
		textareaAInput(e) {
			this.content = e.detail.value
		},
		
		BackPage() {
			uni.navigateBack({
				delta: 1
			});
		}
	}
}
